import React from 'react';
import styled from 'styled-components/macro'
import me1 from '../img/me-1.png';
import me2 from '../img/me-2.png';
import me3 from '../img/me-3.png';

interface ProfilePictureProps {
    slideshowState: number
}

function ProfilePicture({slideshowState}: ProfilePictureProps) {
    return (
        <ImageContainer>
        <Image src={me1} alt="my picture 1" scale={1} show={slideshowState == 0}/>
        <Image src={me2} alt="my picture 2" scale={1} show={slideshowState  == 1}/>
        <Image src={me3} alt="my picture 3" scale={0.7} show={slideshowState  == 2}/>
      </ImageContainer>
    );
}

interface ImageProps {
    show: boolean;
    scale: number;
  };

const ImageContainer = styled.div`
  position: relative;
  width: 100%;
  height: 20rem;
  @media (min-width: 1024px) {
    width: 25rem;
    height: 30rem;
  }
`;

const Image = styled.img<ImageProps>`
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateY(-50%) translateX(-50%);
  transition: opacity 1s linear;
  opacity: ${props=>(props.show) ? 1: 0};
  width: ${(props)=>props.scale * 20}rem;
  @media (min-width: 1024px) {
    width: ${(props)=>props.scale * 25}rem;
  }
`;

export default ProfilePicture;