import React, { useEffect, useState } from 'react';
import './App.css';
import TravelMap from "./TravelMap"
import About from "./About"
import SocialMediaComponent from "./components/SocialMediaComponent";
import ProfilePicture from "./components/ProfilePicture";
import styled from 'styled-components/macro'

function App() {
  const COLORS = ['#3c4854', '#7f2a34', '#735d4d'];
  const [slideshowState, setSlideshowState] = useState(0);

  useEffect(()=>{
    const interval = setInterval(()=>{
      setSlideshowState((prev: number) => (prev + 1) % 3);
    }, 4000)

    return () => {
      clearInterval(interval);
    }
  });

  return (
    <div className="App">
      <header className="App-header">
        <Container>
          <ProfilePicture  slideshowState={slideshowState}/>
          <MainProfileContainer>
            <Name color={COLORS[slideshowState]}>Wei Jie  Lee</Name>
            <Info>Computer Science sophomore at the University of Michigan.</Info>
            <Info>Excited about software, photography, hiking, and the electric guitar!</Info>
            <SocialMediaComponent />
          </MainProfileContainer>
        </Container>
      </header>
      <header className="App-header">
        <Title>Where I've been:</Title>
        <Subtitle>(Click on the clusters!)</Subtitle>
        <TravelMap/>
      </header>

      <header className="App-header">
        <About />
      </header>
    </div>
  );
}

const Container = styled.div`
  @media (min-width: 1024px) {
    display:flex;
  }
`;

const MainProfileContainer = styled.div`
  margin: 4rem 2rem 4rem 2rem;
`;

const Name = styled.h1`
  text-align:left;
  margin-bottom: 0.9rem;
  color: ${props=>props.color} !important;
  transition: color 0.7s linear;
`;

const Info = styled.p`
  text-align:left;
  margin: 0.8rem 0 0rem 0;
  font-size: 0.9em;
`;

const Title = styled.h1`
  font-size: 1em;
  margin:0;
`;

const Subtitle = styled.h2`
  font-size: 0.8rem;
  opacity: 0.8;
`;


export default App;
