import React, { useRef } from 'react';
import './App.css';
import 'mapbox-gl/dist/mapbox-gl.css';
import ReactMapGL, {Layer, Source} from 'react-map-gl';

function TravelMap() {
    const [viewport, setViewport] = React.useState({
        latitude: 0,
        longitude: 0,
        zoom: 1,
        transitionDuration: 800
      });

    const mapRef = useRef(null);

    const onClick = (event:any) => {
        try{
            if (event == null) return;
            if (mapRef == null) return;
    
            const feature = event.features[0];
            const clusterId = feature.properties.cluster_id;
    
            const mapboxSource = (mapRef.current as any).getMap().getSource('places');
    
            mapboxSource.getClusterExpansionZoom(clusterId, (err:any, zoom:number) => {
                if (err || !zoom) {
                    return;
                }
    
                setViewport({
                    ...viewport,
                    longitude: feature.geometry.coordinates[0],
                    latitude: feature.geometry.coordinates[1],
                    zoom
                });
            });
        } catch {
            
        }
    };

    const placeJson: GeoJSON.FeatureCollection = {
        "type": "FeatureCollection",
        "features": []
    }

    const places: any = [
        {lat: 35.6897, lng: 139.6922},
        {lat: 31.1667, lng: 121.4667},
        {lat: 37.5833, lng: 127},
        {lat: 37.5833, lng: 127},
        {lat: 39.905, lng: 116.3914},
        {lat: 22.535, lng: 114.054},
        {lat: 41.8373, lng: -87.6862},
        {lat: 3.1478, lng: 101.6953},
        {lat: 22.305, lng: 114.185},
        {lat: 1.3, lng: 103.8},
        {lat: 43.7417, lng: -79.3733},
        {lat: 38.9047, lng: -77.0163},
        {lat: 42.3834, lng: -83.1024},
        {lat: 22.6167, lng: 120.3},
        {lat: 25.0478, lng: 121.5319}
    ];

    places.map((place: any) => {
        placeJson.features = [
            ...placeJson.features,
            {
                "type": "Feature",
                "geometry": {
                    "type": "Point",
                    "coordinates": [place.lng, place.lat],
                },
                "properties": {}
            }
        ]
    });

  return (
    <ReactMapGL
        {...viewport}
        width="100%"
        height="70vh"
        mapboxApiAccessToken="pk.eyJ1IjoibHdlaWppZSIsImEiOiJja2x0dDR4dzAwY2QxMnBsbzl2eHEyNGtiIn0._IrtLzO15yzx1JBW-v_x9A"
        onViewportChange={(viewport: any) => setViewport(viewport)}
        onClick={onClick}
        ref={mapRef}
    >
        <Source
            id="places"
            type="geojson"
            data={placeJson}
            cluster={true}>
                <Layer type="circle" paint={{"circle-color": "grey", "circle-radius": 10}}/>
        </Source>
    </ReactMapGL>
  );
}

export default TravelMap;
